@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}



@layer base {
  :root {
        --background: 0 0% 100%;
        --foreground: 0 0% 3.9%;
        --card: 0 0% 100%;
        --card-foreground: 0 0% 3.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 0 0% 3.9%;
        --primary: 0 0% 9%;
        --primary-foreground: 0 0% 98%;
        --secondary: 0 0% 96.1%;
        --secondary-foreground: 0 0% 9%;
        --muted: 0 0% 96.1%;
        --muted-foreground: 0 0% 45.1%;
        --accent: 0 0% 96.1%;
        --accent-foreground: 0 0% 9%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 89.8%;
        --input: 0 0% 89.8%;
        --ring: 0 0% 3.9%;
        --chart-1: 12 76% 61%;
        --chart-2: 173 58% 39%;
        --chart-3: 197 37% 24%;
        --chart-4: 43 74% 66%;
        --chart-5: 27 87% 67%;
        --radius: 0.5rem;
    }
  .dark {
        --background: 0 0% 3.9%;
        --foreground: 0 0% 98%;
        --card: 0 0% 3.9%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 3.9%;
        --popover-foreground: 0 0% 98%;
        --primary: 0 0% 98%;
        --primary-foreground: 0 0% 9%;
        --secondary: 0 0% 14.9%;
        --secondary-foreground: 0 0% 98%;
        --muted: 0 0% 14.9%;
        --muted-foreground: 0 0% 63.9%;
        --accent: 0 0% 14.9%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 14.9%;
        --input: 0 0% 14.9%;
        --ring: 0 0% 83.1%;
        --chart-1: 220 70% 50%;
        --chart-2: 160 60% 45%;
        --chart-3: 30 80% 55%;
        --chart-4: 280 65% 60%;
        --chart-5: 340 75% 55%;
    }
}



@layer base {
  * {
    @apply border-border;
    }
  body {
    @apply bg-background text-foreground;
    }
}



  @keyframes float1 {
    0%   { transform: translate(0, 0); }
    25%  { transform: translate(50px, -30px); }
    50%  { transform: translate(0, -60px); }
    75%  { transform: translate(-40px, 30px); }
    100% { transform: translate(0, 0); }
  }
  @keyframes float2 {
    0%   { transform: translate(0, 0); }
    25%  { transform: translate(-40px, 40px); }
    50%  { transform: translate(60px, 0); }
    75%  { transform: translate(-20px, -40px); }
    100% { transform: translate(0, 0); }
  }
  @keyframes float3 {
    0%   { transform: translate(0, 0); }
    25%  { transform: translate(30px, 60px); }
    50%  { transform: translate(-50px, 30px); }
    75%  { transform: translate(20px, -30px); }
    100% { transform: translate(0, 0); }
  }
  @keyframes float4 {
    0%   { transform: translate(0, 0); }
    25%  { transform: translate(-30px, -50px); }
    50%  { transform: translate(40px, -20px); }
    75%  { transform: translate(20px, 40px); }
    100% { transform: translate(0, 0); }
  }
  @keyframes float5 {
    0%   { transform: translate(0, 0); }
    25%  { transform: translate(40px, 20px); }
    50%  { transform: translate(-30px, -40px); }
    75%  { transform: translate(10px, 50px); }
    100% { transform: translate(0, 0); }
  }

  .orb {
    position: absolute;
    animation-duration: 25s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
  }
  .orb1 { top: 20%; left: 25%; animation-name: float1; }
  .orb2 { bottom: 25%; right: 20%; animation-name: float2; }
  .orb3 { top: 50%; left: 50%; animation-name: float3; }
  .orb4 { top: 10%; right: 30%; animation-name: float4; }
  .orb5 { bottom: 15%; left: 15%; animation-name: float5; }

